body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.list-item{
  background-color: #eee;
  margin: 10px;
  display: flex;
  align-items: center;
  border: solid 2px grey;
  border-radius: 60px;
}

.list-item span{
 padding-left: 20px;
 font-size: 25px;
}

.list-item img{
  border-radius: 50%;
}

.header{
  height: 50px;
  background: #aaaaaa;
  display: flex;
  align-items: center;
}

.header button {
  height: 50px;
  background: #aaaaaa;
  border: 0px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 20px;
  color: white;

}

.header button:hover{
  background: grey;
}

.header span{
  margin-left: 500px;
  font-size: 20px;
  font-weight: bold;
  vertical-align: center;
}

.image-holder {
  text-align: center;
}

.image-holder img{
  margin-top: 20px;
  border-radius: 50%;
}

.info-holder{
  padding-top: 20px;
  text-align: center;
  font-size: 20px;
}

.info-holder span{
  font-weight: bold;
}

.d-flex{
  padding-top: 10px;
}